<template>
  <div class="">
    <strong class="chips-down minute">{{ minuteString }}</strong>
    <span>{{ $t('pool.minute') }}</span>
    <strong class="chips-down second">{{ secondString }}</strong>
    <span>{{ $t('pool.second') }}</span>
  </div>
</template>

<script>
import axios from 'axios'
import jsonp from 'jsonp'

export default {
  name: "TimerDown",
  data() {
    return {
      hour: '',
      minute: '',
      second: '',
      timer: '',
      intervalMinute: 10, //
      remainTime: 599, // 
    }
  },
  // props: {
  //   remainTime: {    // 
  //     default: ''
  //   }
  // },
  created() {
    // axios.get('http://api.m.taobao.com/rest/api3.do?api=mtop.common.getTimestamp&callback=jsonCallBack').then(res => {
    //   console.log(res)
    // })

    // this.initTime()
    this.countDown()
  },
  methods: {
    initTime() {
      let nowTime = new Date();
      let rd = Math.floor((nowTime.getHours() * 60 + nowTime.getMinutes()) / this.intervalMinute) + 1;
      let nextTime = new Date(nowTime.getFullYear(), nowTime.getMonth(), nowTime.getDate(), 0, rd * this.intervalMinute, 0)
      this.remainTime = Math.round((nextTime - nowTime) / 1000) - 1;
      if (this.remainTime < 0) {
        this.remainTime = 0;
      }
      // this.hour = Math.floor((this.remainTime / 3600) % 24)
      this.minute = Math.floor((this.remainTime / 60) % 60)
      this.second = Math.floor(this.remainTime % 60)
    },
    countDown() {
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.initTime();
        if (this.minute === 0 && this.second === 0) {
          this.$emit('countDownEnd', true);
          this.countDown();
        }

        this.$emit('timeTick', this.remainTime)

        // if (self.hour === 0) {
        //   if (self.minute !== 0 && self.second === 0) {
        //     self.second = 59
        //     self.minute -= 1
        //   } else if (self.minute === 0 && self.second === 0) {
        //     self.second = 0
        //     clearInterval(self.timer)
        //     self.$emit('countDownEnd', true)
        //     //
        //     self.$nextTick(() => {
        //       // self.remainTime = 599;
        //       self.initTime();
        //       self.countDown();
        //     })
        //   } else {
        //     self.second -= 1
        //   }
        // } else {
        //   if (self.minute !== 0 && self.second === 0) {
        //     self.second = 59
        //     self.minute -= 1
        //   } else if (self.minute === 0 && self.second === 0) {
        //     self.hour -= 1
        //     self.minute = 59
        //     self.second = 59
        //   } else {
        //     self.second -= 1
        //   }
        // }

        // console.log(self.remainTime);

        // self.remainTime--;
      }, 1000)
    },
    formatNum(num) {
      return num < 10 ? '0' + num : '' + num
    }
  },
  computed: {
    hourString() {
      return this.formatNum(this.hour)
    },
    minuteString() {
      return this.formatNum(this.minute)
    },
    secondString() {
      return this.formatNum(this.second)
    }
  }
}
</script>

